<template>
    <a type="text" @click="showModal">前往注册</a>
    <a-modal
            v-model:visible="visible"
            :centered="true"
            :maskClosable="false"
            :footer="null"
            :destroyOnClose="true"
            width="428px"
    >
        <div class="w-100% flex flex-col select-none overflow-hidden">
            <div class="text-20px font-medium mb-16px">注册账号</div>
            <div class="flex gap-16px children-flex-1 children-font-medium children-h-48px children-flex children-items-center children-justify-center">
                <button class="ant-btn" type="button">
                    <WechatOutlined style="font-size: 18px;color:rgb(0,186,97)"/>
                    <span>微信登录</span>
                </button>
            </div>
            <div>
                <div class="ant-divider ant-divider-horizontal ant-divider-with-text ant-divider-with-text-center"
                     role="separator">
                    <span class="ant-divider-inner-text">
                        <span class="color-#C5C5C5 text-12px font-normal">或</span>
                    </span>
                </div>
            </div>
            <a-form
                    :model="formState"
                    name="basic"
                    autocomplete="off"
                    ref="fromRef"
            >
                <a-form-item
                        name="username"
                        :rules="[{ required: true, message: '请输入手机号或邮箱!' },{validator:userNameCheck.bind(this)}]"
                >
                    <a-input v-model:value="formState.username" placeholder="手机号/邮箱">
                        <template #prefix>
                            <UserOutlined style="color: rgba(0, 133, 249, 0.25)"/>
                        </template>
                    </a-input>
                </a-form-item>
                <a-space>
                    <a-form-item style="width: 270px;" name="code"
                                 :rules="[{required: codeRequired, message: '请输入验证码!' }]">
                        <a-input v-model:value="formState.code" placeholder="验证码">
                            <template #prefix>
                                <MessageOutlined style="color: rgba(0, 133, 249, 0.25)"></MessageOutlined>
                            </template>
                        </a-input>
                    </a-form-item>
                    <a-form-item>
                        <a-button type="primary" @click="getCode" :disabled="codeButton" style="width: 102px;">
                            {{ codeButtonText }}
                        </a-button>
                    </a-form-item>
                </a-space>
            </a-form>
            <a-button type="primary" @click="onSubmit">注册</a-button>
            <a-checkbox :checked="clauseChecked" style="margin-top: 16px;margin-left: 16px;" @click="clauseChecked=!clauseChecked">
                <span>
                    <span data-v-b46f1194="">我已阅读并同意</span>
                    <a target="_blank" href="/terms/license" class="color-$primary-color cursor-pointer"
                       data-v-b46f1194=""> 《用户服务协议》 </a>和 <a target="_blank" href="/terms/privacy"
                                                                    class="color-$primary-color cursor-pointer"
                                                                    data-v-b46f1194=""> 《隐私政策》 </a>
                </span>
            </a-checkbox>
            <div class="self-center" style="margin-top: 16px;">
                <span>已有账号？</span>
                <span class="color-$primary-color cursor-pointer" @click="showLoginModal"> 立即登录 </span>
            </div>
        </div>

    </a-modal>
</template>

<script>
import {UserOutlined, MessageOutlined, WechatOutlined} from "@ant-design/icons-vue";
import {reactive, ref} from "vue";
import {message} from "ant-design-vue";

export default {
    name: "RegisterAlert",
    components: {UserOutlined, WechatOutlined, MessageOutlined},
    data() {
        return {
            visible: false
        }

    },
    emits: ['hideModal', 'showModal'],
    methods: {
        //弹窗显示
        showModal() {
            //关闭父级页面的弹窗
            this.$emit('hideModal');
            //显示自己的弹窗
            this.visible = true;
        },
        //弹窗消失
        hideModal() {
            this.visible = false;
        },
        showLoginModal() {
            //关闭自己的弹窗
            this.hideModal();
            //显示父级的登录弹窗
            this.$emit('showModal');
        },
    },
    setup() {
        //login
        const formState = reactive({
            username: '',
            code: '',
            remember: true,
        });
        const fromRef = ref(null)  //用来保存上边a-form中的ref
        const codeRequired = ref(true)  //验证码默认为必填项，点击获取验证码时为非必填
        const clauseChecked = ref(false);
        const codeButton = ref(false);
        const codeButtonText = ref("获取验证码");
        //设置消息的全局参数
        message.config({
            maxCount: 3,
            duration:2,//自动关闭的延时，单位秒。设为 0 时不自动关闭。
        });
        const onSubmit = () => {
            if (!clauseChecked.value){
                message.warning('请先阅读并同意用户服务协议和隐私政策');
                return
            }
            codeRequired.value = true;//改为必填
            //设置200毫秒的延迟，避免上面的参数未生效时就触发验证
            setTimeout(function () {
                fromRef.value.validate().then((values) => {
                    console.log('Success:', values);
                }).catch((err) => {
                    console.log('Failed:', err);
                });
            }, 50);
        }
        //获取验证码
        const getCode = () => {
            codeRequired.value = false;//改为非必填
            //设置200毫秒的延迟，避免上面的参数未生效时就触发验证
            setTimeout(function () {
                fromRef.value.validate().then((values) => {
                    //发送post请求到后台
                    console.log('校验成功，发送post请求，请求参数:', values);
                    //成功后，按钮置灰，并且显示倒计时（这些需放到ajax里面）
                    codeButton.value = true;
                    let time = 10;//倒计时秒数
                    codeButtonText.value = time + "s";
                    let timer = setInterval(function () {
                        time--;
                        if (time <= 0) {
                            clearInterval(timer);
                            codeButton.value = false;
                            codeButtonText.value = "获取验证码";
                            return
                        }
                        codeButtonText.value = time + "s";
                    }, 1000);
                    //倒计时 end
                }).catch((err) => {
                    console.log('Failed:', err);
                });
            }, 50);
        }
        //end-login

        //校验规则
        const userNameCheck=(rule, value) =>{
            // 电话号码正则表达式（支持手机号码，3-4位区号，7-8位直播号码，1－4位分机号）
            //const reg =/^(\d{11})|^((\d{7,8})|(\d{4}|\d{3})-(\d{7,8})|(\d{4}|\d{3})-(\d{7,8})-(\d{4}|\d{3}|\d{2}|\d{1})|(\d{7,8})-(\d{4}|\d{3}|\d{2}|\d{1}))$/;

            const regPhone = /^1[3-9]\d{9}$/;// 正则表达式（只支持手机号码）
            const regEmail = /^\w{3,}(\.\w+)*@[A-z0-9]+(\.[A-z]{2,5}){1,2}$/;
            if (value !=="" && !regPhone.test(value) && !regEmail.test(value)) {
                //注意：新版的Antd使用了React的hooks，所以不能再用 callback
                return Promise.reject("请输入正确的手机号或邮箱");
            }
            return Promise.resolve();
        }

        return {
            formState,
            fromRef,
            onSubmit,
            codeRequired,
            getCode,
            codeButton,
            codeButtonText,
            userNameCheck,
            clauseChecked,
        }
    }
}
</script>

